function bingoSetupNew(commonItems,uncommonItems,rareItems, special){
  var commonItemsList=commonItems.map((item)=>item);
  var uncommonItemsList=uncommonItems.map((item)=>item);
  var rareItemsList=rareItems.map((item)=>item);
  var itemList=[];
  let itemIndex;
  let listPick;
  const setupList=[];
  for (var i=0;i<24;i++){
    if(special==="URBriar" && i===0){
      let briarRoll=Math.floor(Math.random()*1000);
      if(briarRoll===1){
        setupList.push(
          {"id": 105,
            "item": "UR Square \n Briar shows up"
          })
          special="";
          continue;
        }
        
      }
      if(special==="finale" && i===0){
        setupList.push(
          {"id": 106,
            "item": "Second free square for finale"
          })
          special="";
          continue;
        }
        if(special==="Halloween1" && i===0){
          let halloweenRoll=Math.floor(Math.random()*7);
          if ((halloweenRoll)===0){
            setupList.push(
              {"id": 107,
                "item": "Vogun is the traitor"
              })
          }
          if ((halloweenRoll)===1){
            setupList.push(
              {"id": 108,
                "item": "Avianna is the traitor"
              })
          }
          if ((halloweenRoll)===2){
            setupList.push(
              {"id": 109,
                "item": "Lexie is the traitor"
              })
          }
          if ((halloweenRoll)===3){
            setupList.push(
              {"id": 110,
                "item": "Merihim is the traitor"
              })
          }
          if ((halloweenRoll)===4){
            setupList.push(
              {"id": 111,
                "item": "Quintus is the traitor"
              })
          }
          if ((halloweenRoll)===5){
            setupList.push(
              {"id": 112,
                "item": "Fenris is the traitor"
              })
          }
          if ((halloweenRoll)===6){
            setupList.push(
              {"id": 113,
                "item": "Flux is the traitor"
              })
          }
          if ((halloweenRoll)===7){
            setupList.push(
              {"id": 114,
                "item": "Second free square for finale"
              })
          }
            special="Halloween2";
            continue;
          }
          if(special==="Halloween2" && i===1){
              setupList.push(
                {"id": 115,
                  "item": "The Traitor wins"
                })
              setupList.push(
                {"id": 116,
                  "item": "The Traitor loses"
                })
            i++
              continue;
            }
            
        listPick=Math.floor(Math.random()*46);
        if((listPick<36 && commonItemsList.length>0) || (listPick>35 && listPick<46 && uncommonItemsList.length===0)){
          itemIndex=Math.floor(Math.random()*commonItemsList.length);
          setupList.push(commonItemsList[itemIndex]);
          commonItemsList.splice(itemIndex,1);
        }
        else if((listPick>35 && listPick<46) || (listPick<36 && commonItemsList.length===0)){
          itemIndex=Math.floor(Math.random()*uncommonItemsList.length);
          setupList.push(uncommonItemsList[itemIndex]);
          uncommonItemsList.splice(itemIndex,1);
        }
        else{
  
            i--;
        }
      }
      for (i=0;i<25;i++){
        // console.log(i+" is i")
        if(i===12){
          // console.log("huh")
          itemList.push(
            {"id": 0,
              "item": "Free Space"
            });
          }
          else{
            
            itemIndex=Math.floor(Math.random()*setupList.length);
            /* console.log("list length: " + bingoItemsList.length)
            console.log("item index: " + itemIndex)
            console.log(itemIndex) */
            itemList.push(setupList[itemIndex]);
            setupList.splice(itemIndex,1);
          }
        }
        console.log(itemList)
        return(itemList);
      }

      
     
    export default function BingoDisplay() {

      const [toggle, setToggle]=useState(new Array(25).fill(false))
      const [bingoList,setBingo]=useState(bingoSetupNew(halloweenCommon,halloweenUncommon,halloweenRare,"Halloween1"))
      const handleToggle=(index)=>{
        setToggle(toggle.filter((_,i)=>i<index).concat(!toggle[index],toggle.filter((_,i)=>i>index)))
      }
      return (
        
        <>
        
        <html>
<head>
</head>
<body id="viewport">
<BingoHeader/>
<NoticeHeader/>
<div id="bingo-card">
  
        {bingoList.map((item, index)=>
        <div class={`${toggle[index] ? "square stamped":"square"}`} onClick={() => handleToggle(index)} key={index} id={item.item}>
        <span class="square-text" >{item.item}
        </span>
      </div>
    )}
        </div>
<script type="text/javascript">


</script>
<script>
  console.log(document.querySelectorAll(".square"))
</script>

  
<GgFooter/>
</body>



</html>
  
        

        
        </>
      )};
      
      
      
      
      
      